import React from 'react'

const NavBar = ({ sobreMi, skills, trabajos, contacto, lenguaje }) => {
  return (
    <div className='botones'>
      <button
        className='boton'
        onClick={() => {
          window.scrollTo({
            top: sobreMi.current.offsetTop,
            behavior: 'smooth'
          })
        }}
      >
        <div>{lenguaje === 'es' ? 'Sobre Mi' : 'About Me'}</div>
      </button>
      <button
        className='boton'
        onClick={() => {
          window.scrollTo({
            top: skills.current.offsetTop,
            behavior: 'smooth'
          })
        }}
      >
        <div>Skills</div>
      </button>
      <button
        className='boton'
        onClick={() => {
          window.scrollTo({
            top: trabajos.current.offsetTop,
            behavior: 'smooth'
          })
        }}
      >
        <div>{lenguaje === 'es' ? 'Portfolio' : 'My Works'}</div>
      </button>
      <button
        className='boton'
        onClick={() => {
          window.scrollTo({
            top: contacto.current.offsetTop,
            behavior: 'smooth'
          })
        }}
      >
        <div className='sm-4'>
          <div>{lenguaje === 'es' ? 'Contacto' : 'Contact Me'}</div>
        </div>
      </button>
    </div>
  )
}
export default NavBar
