import React from 'react'
import qr from '../../videos/qr.mp4'
import AppMovil from './AppMovil'

const Qr = props => {
  const { lenguaje } = props
  return (
    <div {...props}>
      <AppMovil
        lenguaje={lenguaje}
        titulo={lenguaje === 'es' ? 'Lector de Qr' : 'Qr Reader'}
        descripcion={
          lenguaje === 'es'
            ? 'Aplicación parte del curso de Flutter de Fernando Herrera en Udemy. Lector de Qr con mapas integrados y acceso al navegador.'
            : "App of the Flutter's course of Fernando Herrera in Udemy. Qr reader with integrated maps and access to the web browser."
        }
        video={qr}
        lenguajes={['Flutter']}
      />
    </div>
  )
}
export default Qr
