import React from 'react'

const SobreMi = props => {
  return (
    <div {...props} className='pagina sobre-mi'>
      <h4 className='text-center text-uppercase titulo'>
        {props.lenguaje === 'es' ? 'Sobre Mi' : 'About Me'}
      </h4>
      <div>
        <blockquote className='texto-sobreMi'>
          {props.lenguaje === 'es'
            ? ' Mi nombre es Facundo Gaston Lazcano Colodrero. Tengo 25 años y soy de Córdoba, Argentina. Mis principales skills son en el Frontent pero tengo conocimientos de Fullstack como también de herramientas de diseño. Me considero una persona con capacidad para adaptarme a los          cambios contantes. Soy autodidacta, con capacidad y rapidez para aprender nuevos lenguajes y comenzar a aplicarlos. Me siento preparado para ingresar el mundo IT y aportar desde mi lugar, como así también seguir adquiriendo conocimientos y habilidades diariamente.'
            : "Mi name is Facundo Gaston Lazcano Colodrero. I'm 25 years old and i'm from Córdoba, Argentina. My hard skills are on the Frontend but i also have knownledge about Fullstack and design programs. I consider myself as a person with the capability to adapt to the constants changes. I'm self-learning, with capability and agility to learn new languages and start to use them. I feel i'm prepared to get into the IT world and contribute from my position as well as continue acquiring knowledge and skills everyday"}
        </blockquote>
      </div>
    </div>
  )
}

export default SobreMi
