import React from 'react'
import peliculas from '../../videos/peliculas.mp4'
import AppMovil from './AppMovil'

const Peliculas = props => {
  const { lenguaje } = props
  return (
    <div {...props}>
      <AppMovil
        lenguaje={lenguaje}
        titulo={lenguaje === 'es' ? 'Aplicación de Peliculas' : 'Movies App'}
        descripcion={
          lenguaje === 'es'
            ? 'Aplicación parte del curso de Flutter de Fernando Herrera en Udemy con datos de la API de themoviedb.'
            : "App part of the Flutter's course of Fernando Herrera in Udemy"
        }
        video={peliculas}
        lenguajes={['Flutter']}
      />
    </div>
  )
}

export default Peliculas
