import React, { useRef, useEffect, useState } from 'react'
import Header from './Header'
import SobreMi from './SobreMi'
import Trabajos from './Trabajos'
import Contacto from './Contacto'
import NavBar from './NavBar'
import Aos from 'aos'
import 'aos/dist/aos.css'
import Skills from './Skills'
import { useWindowScroll } from 'react-use'
import { KeyboardArrowUp } from '@material-ui/icons'
import us from '../banderas/us.png'
import mx from '../banderas/mx.png'
require('./App.css')
require('./Contacto.css')
require('./Skils.css')
require('./SobreMi.css')
require('./NavBar.css')
require('./Header.css')
require('./Portfolio.css')

const App = () => {
  const [lenguaje, setLenguaje] = useState('es')
  const { y } = useWindowScroll()
  console.log(lenguaje)

  useEffect(() => {
    Aos.init({
      easing: 'ease-out',
      duration: 1000
    })
    Aos.refresh()
  }, [])
  const sobreMi = useRef(null)
  const skills = useRef(null)
  const trabajos = useRef(null)
  const contacto = useRef(null)

  const buttonToTop = () => {
    if (y > 508) {
      return (
        <div
          className='circulo'
          onClick={() => {
            window.scrollTo({ top: 0, behavior: 'smooth' })
          }}
        >
          <KeyboardArrowUp style={{ marginLeft: 3 }} />
        </div>
      )
    } else {
      return null
    }
  }
  const renderBotonLenguaje = () => {
    return (
      <div className='btn-lng'>
        <img
          src={mx}
          className='bandera'
          alt='bandera-es'
          onClick={() => setLenguaje('es')}
        />
        <img
          src={us}
          className='bandera'
          alt='bandera-es'
          onClick={() => setLenguaje('en')}
        />
      </div>
    )
  }

  return (
    <div className='fondo'>
      {renderBotonLenguaje()}
      <NavBar
        lenguaje={lenguaje}
        sobreMi={sobreMi}
        skills={skills}
        trabajos={trabajos}
        contacto={contacto}
      />
      {buttonToTop()}
      <div>
        <Header />
        <div data-aos='fade-right' ref={sobreMi}>
          <SobreMi lenguaje={lenguaje} />
        </div>
        <div data-aos='fade-down' ref={skills}>
          <Skills />
        </div>
        <div data-aos='flip-left' ref={trabajos}>
          <Trabajos lenguaje={lenguaje} />
        </div>
        <div data-aos='fade-up-right' ref={contacto}>
          <Contacto lenguaje={lenguaje} />
        </div>
      </div>
    </div>
  )
}

export default App
