import React from 'react'

const AppMovil = ({ video, titulo, descripcion, lenguajes, lenguaje }) => {
  return (
    <div>
      <div className='video-pagina'>
        <div className='proyecto'>
          <video src={video} className='celular' controls autoPlay />
          <div className='descripcion-proyecto'>
            <h4>{titulo}</h4>
            <div className='descripcion'>{descripcion}</div>
            <div style={{ fontWeight: 'bold' }}>
              {lenguaje === 'es' ? 'Lenguajes Utilizados:' : 'Used Languages:'}
            </div>
            <div className='lenguajes-total'>
              <div className='lenguaje-usado'>{lenguajes}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default AppMovil
