import React from 'react'
import WebPage from './WebPage'
import lazcano from '../../videos/lazcanoweb.png'

const Lazcano = ({ lenguaje }) => {
  const lenguajes = [
    { name: 'ReactJS' },
    { name: 'NodeJs' },
    { name: 'Express' },
    { name: 'MongoDB' },
    { name: 'Moongose' }
  ]
  return (
    <WebPage
      lenguaje={lenguaje}
      titulo='Lazcano Colodrero | Pieri Conti'
      descripcion={
        lenguaje === 'es'
          ? 'Página web de estudio jurídico y escribanía. Primer proyecto personal.'
          : "Web page of legal study and notary's office. First personal project"
      }
      lenguajes={lenguajes}
      imagenWeb={lazcano}
    />
  )
}

export default Lazcano
